@import url(https://fonts.googleapis.com/css2?family=Rock+Salt&family=ZCOOL+XiaoWei&display=swap);
body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.board-container{
    width: 100%;
    height: calc(100vh - 70px);
    display: flex;
    flex-wrap: wrap;
}

.profile-container{display:flex;flex:3 1;justify-content:center;align-items:center}.profile-container .profile-img{height:400px;width:400px;background-image:url(/static/media/index.571f1be4.jpg);background-size:cover;background-repeat:no-repeat;border:none;border-radius:50%}.intro-container{flex:2 1;display:flex;flex-direction:column;justify-content:center;padding:0 30px}.intro-container h1{font-size:2.2rem;margin:20px 0}.intro-container p{font-size:1.2rem;line-height:35px}.intro-container a{display:flex;justify-content:center;align-items:center;height:40px;width:100px;margin:20px 0;background:#3498db;border:none;border-radius:4px;outline:none;cursor:pointer;text-decoration:none;font-size:1.2rem;font-weight:700;color:#fff}.intro-container a:hover{background:#f39c12}.intro-container a:hover a{color:#34495e}@media screen and (max-width: 786px){.profile-container{margin-top:15px}.profile-img{max-width:80vw;max-height:80vw}}

.nav-bar{width:100%;background:#3498db;color:white;height:70px;display:flex;justify-content:space-between;align-items:center}.nav-bar .logo{flex:3 1;text-align:center;display:inline-block;margin-left:2rem}.nav-bar .logo a{text-decoration:none;color:#fff;font-size:2rem;font-weight:700}.nav-bar .nav-links{flex:2 1;display:flex;margin-right:100px;align-items:center;list-style:none}.nav-bar .nav-links li{margin:0 22px}.nav-bar .nav-links li a{text-decoration:none;color:#fff;font-size:1.6rem;font-weight:500}.nav-bar .nav-links li:hover a{color:#f1c40f}.nav-bar .hamburger{display:none;position:relative;top:0;right:20px}.nav-bar .hamburger div{display:flex;flex-direction:column;background-color:black;margin:4px 0;width:25px;height:4px}.nav-bar .x{position:relative;top:0;right:20px}.nav-bar .x div{margin:-1px;background-color:black;width:25px;height:4px}.nav-bar .x #x1{position:relative;right:0;top:3.1px;transform:rotate(45deg)}.nav-bar .x #x2{transform:rotate(-45deg)}@media screen and (max-width: 780px){.hamburger{display:block !important}.logo{margin-left:0 !important}.nav-links{transform:translate(-100%);flex:3 1;margin:0 !important;padding-left:50px;height:50px;position:absolute;top:70px;right:0;width:100%;z-index:99;background-color:#fff;overflow-y:auto;justify-content:space-between}.nav-links li a{color:#7f8c8d !important}.display-nav{transform:translate(0%);transition:0.08s ease-in-out}}

.pad-container{height:100%;flex:5 1;display:flex;justify-content:center;align-items:center}.pad-container .pad{overflow-y:auto;max-height:550px;max-width:420px;display:flex;justify-content:center;flex-wrap:wrap}.pad-container .pad .pad-grid{display:flex;justify-content:center;align-items:center;width:160px;height:160px;margin:6px;font-weight:300;color:#111;border:none;border-radius:10px;transition:all 0.1s ease-in-out;cursor:pointer}.pad-container .pad .pad-grid:hover{transform:scale(1.05);color:#eee}.content-container{flex:4 1;display:flex;justify-content:flex-start;align-items:center}.content-container .content{font-family:'Rock Salt', cursive;font-family:'ZCOOL XiaoWei', serif;font-size:1.5rem;line-height:2.4rem;display:flex;flex-direction:column;justify-content:center;align-items:center;width:450px;max-height:600px;min-height:450px;background-size:30px 30px;background-image:linear-gradient(to right, grey 1px, transparent 1px),linear-gradient(to bottom, grey 1px, transparent 1px);border:solid 0.1px black;border-radius:30px;margin:0 20px}@media screen and (max-width: 780px){.content-container{justify-content:center}.content{margin:0 !important;max-width:90vw;min-height:90vw}}

.command-line-container{flex:3 1;display:flex;justify-content:center;align-items:center}.command-line-container .command-line{overflow-y:auto;height:260px;width:420px;background:black;padding:20px;color:#fff;font-family:Monaco, Menlo, 'Ubuntu Mono', Consolas, source-code-pro, monospace;font-weight:700;border:none;border-radius:10px;outline:none}.command-line-container .command-line .option{cursor:pointer}.command-line-container .command-line .option .selected{color:#f1c40f}.panel-container{flex:2 1;display:flex;align-items:center}.panel-container .panel{max-width:600px}.panel-container .panel .singlework-container{margin-right:15px}.panel-container .panel .singlework-container img{border:none;border-radius:10px;width:420px;height:250px}.panel-container .panel .singlework-container h3{padding-top:10px;font-size:1.3rem;text-align:center}.panel-container .panel .allworks-container{display:flex;flex-wrap:wrap;align-items:center}.panel-container .panel .allworks-container img{border:#bdc3c7 solid 5px;border-radius:5px;width:200px;height:120px;margin:8px 5px;padding:15px}@media screen and (max-width: 780px){.command-line{margin-top:15px;max-width:80vw}.singlework-container{margin-right:0 !important}.singlework-container img{border:none;border-radius:10px;max-width:80vw;max-height:48vw;min-height:48vw}.singlework-container h3{padding-top:10px;font-size:1.3rem;text-align:center}.panel{margin-top:20px}.panel-container{justify-content:center}.allworks-container{align-items:center;justify-content:center}}

